<template>
  <div>
    <p class="tips">
      <span>{{type}}</span>
      <a :href="href" class="tips__href" target="_blank"></a>
    </p>
    <div class="upload">
      <div class="upload__label">
        <img src="../assets/img/excel.jpg"
             class="upload__img">
        <p class="upload__tips" v-show="!fileName">xlsxlsx</p>
        <p class="file">{{fileName}}</p>
        <label for="file-input"
               class="btn-main"
               @click.stop="btnUploadClick"></label>
      </div>
      <input type="file" id="file-input" hidden
             accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
             @change="onFileChange">
    </div>
  </div>
</template>

<script>
  export default {
    // data(){
    //   return {
    //     fileName: null
    //   }
    // },
    props: {
      // 
      type: {
        type: String,
        required: true
      }
    },
    data () {
      return {
        text: '',
        fileName: null
      }
    },
    computed: {
      suffix () {
        let xlsx = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
        return xlsx.indexOf(this.type) > -1 ? 'xlsx' : 'xls'
      },
      href () {
        return `../gy-files/template/${this.type}.${this.suffix}`
      }
    },
    methods: {
      // 
      btnUploadClick (e) {
        this.$emit('btn-click', e)
      },
      // file-inputchange
      onFileChange (e) {
        this.$emit('change', e)
        this.fileName = e.target.files[0].name
        // input, change
        let oFileInput = document.getElementById('file-input')
        oFileInput.value = null
      }
    }
  }
</script>

