Vue.prototype.$cloneObj = function clone (item) {
  if (!item) {
    return item
  } // null, undefined values check

  let types = [Number, String, Boolean]
  let result = null

  // normalizing primitives if someone did new String('aaa'), or new Number('444');
  // new
  // new String('aaa'), or new Number('444')
  types.forEach(type => {
    if (item instanceof type) {
      result = type(item)
    }
  })

  if (result === null) {
    if (Object.prototype.toString.call(item) === '[object Array]') {
      result = []
      item.forEach(function (child, index, array) {
        result[index] = clone(child)
      })
    } else if (typeof item === 'object') {
      // testign that this is DOM
      //domcloneNode
      if (item.nodeType && typeof item.cloneNode === 'function') {
        let result = item.cloneNode(true)
      } else if (!item.prototype) { // check that this is a literal
        // it is an object literal
        //for in 
        result = {}
        for (let i in item) {
          if (item.hasOwnProperty(i)) {
            result[i] = clone(item[i])
          }
        }
      } else {
        // depending what you would like here,
        // just keep the reference, or create new object
        //false && 
        //new
        if (false && item.constructor) {
          // would not advice to do that, reason? Read below
          //new
          result = new item.constructor()
        } else {
          result = item
        }
      }
    } else {
      result = item
    }
  }
  return result
}

// cookie
Window.prototype.$Cookie = {
  //cookie
  setCookie (name, value, hours, path) {
    var name = escape(name) //
    var value = escape(value)//
    var expires = new Date()
    expires.setTime(expires.getTime() + hours * 3600000)
    path = path == '' ? '' : ';path=' + path
    var _expires = (typeof hours) == 'string' ? '' : ';expires=' + expires.toUTCString()
    document.cookie = name + '=' + value + _expires + path
  },
  //cookie
  getCookieValue (name) {
    var name = escape(name) //
    //cookiecookie
    var allcookies = document.cookie
    //namecookie
    name += '='
    var pos = allcookies.indexOf(name)
    //cookie
    if (pos != -1) {                       //pos-1"version="
      var start = pos + name.length         //cookie
      var end = allcookies.indexOf(';', start)    //cookie";",cookie
      if (end == -1) end = allcookies.length    //end-1cookiecookie
      var value = allcookies.substring(start, end) //cookie
      value = unescape(value)  //
      return (value)
    }
    else return ''                //
  },
  //cookie
  deleteCookie (name, path) {
    var name = escape(name)
    var expires = new Date(0)
    path = path == '' ? '' : ';path=' + path
    document.cookie = name + '=' + ';expires=' + expires.toUTCString() + path
  },
  // cookie
  delectAllCookie () {
    let keys = document.cookie.match(/[^ =;]+(?=\=)/g)
    if (keys) {
      for (var i = keys.length; i--;)
        document.cookie = keys[i] + '=0;expires=' + new Date(0).toUTCString()
    }
  }
}

